<template>
  <div class="SnippetExplorer">
    <Search v-if="snippets" />
    <Snippets
      :snippets="snippets"
      @select="selectSnippet"
    />
    <BaseButton
      round
      icon
    >
      <svg svg-inline="" @click="handleClick" role="presentation" focusable="false" xmlns="http://www.w3.org/2000/svg" width="18" height="19" viewBox="0 0 18 19"><g fill="none" fill-rule="evenodd" stroke="#FFF" stroke-linecap="square" stroke-width="3"><path d="M16.5 9.5h-15M9 2v15"/></g></svg>
    </BaseButton>
  </div>
</template>
<script>
import uniqid from 'uniqid';  
import { mapActions } from 'vuex';
import BaseButton from '../components/BaseButton.vue';
import Search from '../components/snippetExplorer/Search.vue';
import Snippets from '../components/snippetExplorer/Snippets.vue';

export default {
  name: 'SnippetExplorer',
  components: {
    BaseButton,
    Search,
    Snippets,
  },
  props: {
    snippets: Array,
  },
  data: () => ({}),
  methods: {
    handleClick() {
      let uID = uniqid();
      this.addSnippet(uID);
    },
    ...mapActions([
      'selectSnippet',
      'addSnippet'
    ])
  }
};

</script>
